"use client";

import React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { cn } from '@/lib/utils';
import { adminLogout } from '@/actions/admin-auth';
import {
    LayoutDashboard,
    Store,
    CreditCard,
    Megaphone,
    Ticket,
    LogOut
} from 'lucide-react';
import { Button } from '@/components/ui/button';

const navItems = [
    { name: 'Dashboard', href: '/admin', icon: LayoutDashboard },
    { name: 'Studios', href: '/admin/studios', icon: Store },
    { name: 'Payments', href: '/admin/payments', icon: CreditCard },
    { name: 'Campaigns', href: '/admin/campaigns', icon: Megaphone },
    { name: 'Coupons (Activa)', href: '/admin/coupons', icon: Ticket },
];

export function AdminSidebar() {
    const pathname = usePathname();

    const handleLogout = async () => {
        await adminLogout();
    };

    return (
        <aside className="w-64 bg-slate-900 border-r border-white/10 flex flex-col h-screen fixed left-0 top-0 overflow-y-auto">
            <div className="p-6 border-b border-white/10">
                <div className="text-xl font-bold tracking-tighter text-cyan-400">
                    ANTI<span className="text-white">GRAVITY</span>
                </div>
                <div className="text-xs text-gray-500 tracking-widest mt-1">ADMIN CONSOLE</div>
            </div>

            <nav className="flex-1 p-4 space-y-2">
                {navItems.map((item) => {
                    const isActive = pathname === item.href || (item.href !== '/admin' && pathname.startsWith(item.href));
                    return (
                        <Link key={item.href} href={item.href}>
                            <div className={cn(
                                "flex items-center gap-3 px-4 py-3 rounded-lg text-sm font-medium transition-all",
                                isActive
                                    ? "bg-cyan-900/20 text-cyan-400 border border-cyan-500/20"
                                    : "text-gray-400 hover:text-white hover:bg-white/5"
                            )}>
                                <item.icon className="w-4 h-4" />
                                {item.name}
                            </div>
                        </Link>
                    );
                })}
            </nav>

            <div className="p-4 border-t border-white/10">
                <Button
                    variant="ghost"
                    className="w-full justify-start text-red-400 hover:text-red-300 hover:bg-red-900/10"
                    onClick={handleLogout}
                >
                    <LogOut className="w-4 h-4 mr-2" />
                    Sign Out
                </Button>
            </div>
        </aside>
    );
}
